.Profile {
    margin: 100px 300px 40px 300px;
    display: block;
    justify-content: center;
}

.Feeship {
    background-color: white;
    padding:30px;
    border-radius: 10px;
}


.feeship-img {
    width: 100%;
}

.title-feeship {
    text-align: center;
}

/* Mobile: width < 740px */
@media only screen and (max-width:46.1875em) {
    .Profile {
        padding: 20px;
        margin: 0;
        margin-top: 70px; 
    }

    .Profile > h3 {
        font-size: medium;
    }


}