/* ===Design for Laptop=== */

.Login {
  background-image: url(https://drive.google.com/uc?export=view&id=1v6enOzhQoy1NOT5nSLyX-vGR_gJsXwni);
  background-size: cover;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 103;
  font-size: small;
  height: 100vh;
}

.login-box {
  max-width: 380px;
  width: 100%;
  margin: 0 45% 0 55%;
  padding: 1rem;
  height: 100%;
  display: flex;
}

.sign-up {
  cursor: pointer;
}

.login-center {
  background-color: #fff;
  list-style: none;
  line-height: 2rem;
  width: 100%;
  height: auto;
  margin: auto;
  padding: 2rem;
  border: 0.2px solid rgba(174, 174, 174, 0.553);
  border-radius: 10px;
  box-shadow: 5px 5px 1px 1px #b8b8b844;
  display: grid;
  grid-template-rows: repeat(5, auto);
  row-gap: 1rem;
}

.login-center .title__mobiles, .image__mobiles {
  display: none;
}

.login-center h3 {
  text-align: center;
}

.login-center p {
  text-align: center;
}

.login-center button {
  background-color: #76a2f5;
  color: white;
  border: none;
}

.login-center button:hover {
  background-color: #caddff;
}

.hide-login {
  display: none;
}

/* ==== Design for Mobile ====  */

/* Mobile: width < 740px */
@media only screen and (max-width:40.1875em) {
  .Login {
    background-image:none;
    background-size: cover;
    /* max-height: 100vh; */
  }

  .login-box {
    padding: 0;
    margin: 0 auto;
    position: relative;
    max-width: 100%;
    width: 100%;
  }

  .login-center {
    position: absolute;
    display: grid;
    grid-template-rows: repeat(6, auto);
    top: 49vh;
    bottom: 0;
    width: 100%;
    border-radius: 5;
    border: none;
  }

  .login-center h3 {
    display: none;
  }

  .login-center .title__mobiles {
    display: block;
    text-align: left;
  }

  .login-box .image__mobiles {
    display: block;
    width: 100%;
    max-width: 100%;
    position: absolute; 
    top:0;left:0;right:0;bottom: 0;
  }

  .login-center .title__mobiles h3,
  p {
    display: block;
    text-align: left;
  }
}
