.bag {
    margin-top: 100px;
    display: flex;
    justify-content: center;
    text-align: center;
}

.orderproduct {
    background-color: white;
    border-radius: 5px;
    padding: 50px 70px 100px 70px;
    width: 100%;
    font-size: medium;
    position: relative;
    margin: 0px 150px 20px 150px;
}

.champion {
    width: 50%;
}


.order-content {
    padding: 20px;
}


.order-items {
  display: block;
}

.card {
    box-shadow: 12px 12px 2px 1px rgba(199, 199, 199, 0.2);
}

.try {
    display: grid;
    grid-template-columns: repeat(2,auto);
    
}

.ele-orders {
    display: block;
    text-align: right;
    font-size: medium;
}

.bg-color {
    color: rgb(127, 136, 255);
    text-decoration: underline;
}



.item-orders-img {
    border: 0.5px solid rgba(189, 189, 189, 0.479);
    border-radius: 5px;
    padding: 5px;
    width: 100px;
}


.cash-back {
    z-index: 2;
    position: absolute;
    right: 30px;
    top: 30px;
}

.withdraw {
    padding: 20px;
}

.confirm-form {
    padding: 10px;
}

.leader-mobile {
    display: none;
}


.bonus-success {
    background-color: chartreuse;
    padding: 3px;
    border-radius: 20px;
    color:rgb(63, 63, 63);
    font-size: small;
    font-weight: bold;
    box-shadow: 5px 4px 4px  rgba(199, 199, 199, 0.5);

}

.quy-prize {  
    background-image: url(https://lh3.googleusercontent.com/SnkGVfqcE-Il84qUtFHenzs_XsdGgWBmFAwzW_1HRwxyglY9z5AqJDhnO7Farf7m-sghCGqLiOvlrEqAczPiSqs6jiF37TmOS2pERPC5r8MQb5KrnqrU7ybtx5wllvR4fEQpoEQT7Q=w2400); 
    background-size: cover;
    height: 350px;
    width: 100%;
}

.quy-hide{
    display: none;
}





/* Mobile: width < 740px */
@media only screen and (max-width:46.1875em) {

    .bag {
        margin-top: 100px;
        margin-bottom: 10px;
    }

    .champion {
        margin-top: 30px;
    }

    .orderproduct {
        font-size: medium;
        padding: 5px;
        padding-bottom: 20px;
        margin: 0;
    }

    .leader-pc {
        display: none;
    }


    .prize {
        padding: 20px;
        display: grid;
        grid-template-columns: repeat(2,auto);
        border: 0.5px solid rgba(189, 189, 189, 0.479);
        margin-top: 5px;
        border-radius: 10px;
        box-shadow: 2px 2px 2px 1px rgba(199, 199, 199, 0.2);
        position: relative;
    }



    .img-prize {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 90px;
    }

    .info-prize {
        text-align: left;
        width: 100%;
        font-size: small;
    }

    .info-prize > ul {
        margin-left: 25px;
    }

    .info-prize > h6 {
        font-size: large;
        color:rgb(86, 95, 218);
    }


    .detail-prize {
        display: flex;
        justify-content: right;
    }

    .leader-mobile {
        display: block;
    }


    .orderproduct > h2 {
        font-size: x-large;
    }

    .cash-back {
        position: fixed;
        top:auto;
        bottom: 70px;
        right:30px;
        border-radius: 50px;
        height: 50px;
        box-shadow: 1px 2px 0px  rgba(199, 199, 199, 0.404);
    }

    .text-cash {
        display: none;
    }


    .close {
        position: absolute;
        right: 20px;
        top: 15px;
        font-size: xx-large;
    }

    .quy-prize {  
        height: 180px;
        width: 100%;
    }

  }
  