.footer {
    background-color: rgba(255, 255, 255, 0.87);
    text-align: center;
    padding: 30px 0px 30px 0px;
    box-shadow:10px 10px 0 10px grey;  
}

.footer-name {
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}


.descrip {
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Mobile: width < 740px */
@media only screen and (max-width:46.1875em) {
    .footer-name {
        font-size: 30px;
    }


    .footer {
        padding: 10px;
        font-size: small;
    }
}

