.slide-container {
    width: 70%;
    margin: auto; 
    border-radius: 10px;
}

  
.each-slide > div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    height: 500px;
    border-radius: 10px;
  }
  
.each-slide span {
    padding: 20px;
    font-size: 20px;
    background: #efefef;
    text-align: center;
  }


  /* Mobile: width < 740px */
@media only screen and (max-width:46.1875em) {
  .each-slide > div {
    height: 190px;
  }
}

