.NavbarItems {
    background-color: rgb(118, 162, 245);
    padding: 15px 20px 10px 20px;
    color:aliceblue;
    display: grid;
    grid-template-columns: repeat(4,auto);
    position: fixed;
    top:0;
    left: 0;
    right:0;
    z-index:100;
}

.invisible {
    display: none;
}

.Navbar-logo-pc {
    width: auto;
    color: white;
}

.Navbar-logo-mobile {
    display: none;
}


.Navbar-list-items-pc {
    display:flex;
    grid-gap: 20px;
    text-align: center;
    list-style: none;
    font-size: 20px;
    color:white;
    width: 100%;
    justify-content: right;
    height: 30px;
}

.Navbar-list-items-pc > li > a {
    text-decoration: none;
    color:white;
}

.Navbar-list-items-pc > li > a:hover {
    text-decoration: underline;
}


.Navbar-list-items-mobile  {
    display: none;
}

.Navbar__mobile {
    display: none;
}


.search-mobile {
    display: none;
}


.finding-product-mobile {
    display: none;
}



/* Mobile: width < 740px */
@media only screen and (max-width:46.1875em) {
    .NavbarItems {
        display: flex;
    }


    .Navbar-logo-pc {
        display: none;
    }

    .Navbar-logo-mobile {
        display: block;
    }


    .Navbar-list-items-pc {
        display: none;
    }

    .Navbar-list-items-mobile {
        display: flex;
        justify-content: right;
        align-items: right;
        width: 100%;
        color: white;
    }

    .Navbar-list-items-mobile > a {
        color: white;
        text-decoration: none;
    }

    .search-pc {
        display: none;
    }



    .Navbar__mobile {
        position: fixed;
        top:0; right:0; bottom: 0;
        left:0;
        width: auto;
        height: 100%;
        z-index: 3;
        background-color: white;
        padding: 20px;
    }

    .nav_input {
        display: none;
    }

    .nav_input:checked ~ .finding-product-mobile {
        transform: translateX(0%);
        transition: transform linear 0.3s;
    }

    .nav_input ~ .finding-product-mobile { 
        transform: translateX(100%);
        transition: transform linear 0.3s;
    }

    .nav_input_search {
        width: 100%;
        text-align: center;
    }

    .nav_input_search > a:hover {
        color:rgba(82, 82, 82, 0.671);
        text-decoration: none;    
    }

    .finding-product-mobile {
        position: fixed;
        top:0;
        right:0;
        bottom: 0;
        width: 100%;
        z-index: 10;
        background-color: white;
        display: block;
        transform: translateX(100%);
        color: black;
        overflow-y: scroll;
    }

    .search-box-mobile {
        display: grid;
        grid-template-columns: repeat(2,auto);
        border: 0.5px solid rgba(0, 0, 0, 0.301);
        border-radius: 20px;
        padding: 10px;
        align-items: center;
       
    }

    .search-input-mobile {
        border: none;
        height: 30px;    
    }

    .header-search-box-mobile {
        border-bottom: 0.2px solid rgba(0, 0, 0, 0.1);
        padding: 20px;
    }

    .show_search_result_mobile {
        padding: 20px;
        color:black;
    }

    .suggest-box-mobile {
        text-decoration: none;
        color:gray;
        display: flex;
    }


}

