@import "~react-image-gallery/styles/css/image-gallery.css";

.header-des {
    background-color: #fafafa;
    width: 100%;
    border-radius: 2px;
    font-size: 20px;
    padding:15px 30px 15px 20px;
}


.description-content {
    font-size: 15px;
    padding: 10px 35px 20px 20px;
}

.list-products {
    display: grid;
    grid-template-columns: repeat(2,auto);
    height: 60px;
    font-size: large;
}


.product-info {
    padding: 0px 10px 0px 10px;
}


.right-list-product {
    text-align: right;
    width: 100%;
}


.details-product {
    margin: 100px 120px 30px 120px;

}


.summary-product {
    background-color: white;
    margin-top: 20px;
    max-height: 900px;
    padding: 20px;
    border-radius: 2px;
}

.product-description {
    background-color: white;
    margin-top: 10px;
    max-height: auto;
    padding: 20px;
    border-radius: 2px;
}


.price {
    display: block;
    padding: 20px;
    font-size: larger;
    align-items: center;
}

.ship, .number { 
    display: grid;
    grid-template-columns: repeat(3,auto);
}

.ship {
    width: 90%;
}

.free-ship {
    background-color:  #f1f5fdd3;
    border-radius: 10px;
    margin: 2px;
    padding: 15px;
    height: 70px;
    color: black;
    display: flex;
    width: 200px;
    align-items: center;
}





.none {
    border: 0.2px solid rgba(231, 231, 231, 0.8);
    width: 10px;
}



.info-product {
    padding: 20px 30px 20px 20px;
    display: grid;
    grid-template-rows: repeat(7,auto);
    min-height: 600px;
    height: auto;
    align-items: center;
}

.hide, .form-check-input {
    display: none;
}

.form-check-label { 
    border-radius: 20px;
    padding: 10px;
    color: #6d97e6;
    border: 0.1px solid #6d97e6;
    font-size: small;
}

.form-check-inline {
    margin: 2px 2px 2px 2px;
}

.form-check-label:hover {
   background-color: rgba(225, 224, 224, 0.705); 
}



.number {
    align-items: center;
    padding: 20px;
    width: 350px;
}

.increase {
    display: grid;
    grid-template-columns: repeat(3,auto);
    width: 130px;
    border: 0.2px solid rgba(231, 231, 231, 0.8);
    text-align: center;
}

.ele-increase {
    width: 100%;
    text-align: center;
    height: 34px;
    align-items: center;
}

.add-cart {
    height: 45px;
}


.add-items-cart { 
    border: 0.5px solid #6d97e6;
    width: 230px;
    height: 100%;
    border-radius: 1px;
    color: #6d97e6;
    background-color:#e3edff; 
    padding: 10px;
    font-size: large;
}


.btn:hover {
    border: #4d4d4dc7;
}

.gallery-mobile {
    display: none;
}

.form-check-inline_mobiles {
    display: none;
}


/* Mobile: width < 740px */
@media only screen and (max-width:46.1875em) {
    .details-product {
        width: 100%;
        margin: 100px 0 0 0;
    }

    .route-link {
        width: auto;
        text-align: left;
        font-size: small;
        padding-left: 10px;
    }

    .summary-product {
        font-size: medium;
    }

    .gallery-pc {
        display: none;
    }

    .gallery-mobile {
        display: block;
        width: 100%;
    }

    .info-product {
        display: block;
        font-size: medium;
        width: auto;
        height: auto;
        min-height: auto;
    }


    .info-product > h3 {
        font-size: medium;
        font-weight: bold;
    }


    .price {
        font-size: medium;
        padding: 0;
    }

    .price > b {
        font-size: medium;
    }

    .ship {
        width: 100%;
        font-size: small;
        display: grid;
        grid-template-columns: repeat(3,auto);
    }

    .free-ship {
        padding: 0;
        width: 100px;
        height: auto;
        background-color: white;
        font-size: smaller;
    }

    .free-ship > svg {
        height: 30px;
    }

    .number {
        display: none;
    }

    .add-cart {
        margin-top: 20px;
        width: 100%;
    }
    .add-items-cart {
        width: 100%;
    }

    .header-des {
        font-size: 15px;
    }

    .list-products {
        font-size: medium;
    }

    .description-content {
        font-size: medium;
    }

    .product-description {
        margin-bottom: 10px;
    }

    .description-content {
        font-size: medium;
    }

    .price_classify {
        margin-top: 20px;
    }


    .form-check-inline {
        display: none;
    }

    .form-check-inline_mobiles {
        display: block;
    }

    .pick-cate {
        width: 100%;
    }

}